.form-check {
  margin-bottom: 10px;
}

.form-check-input {
  margin-right: 5px;
}

.custom-multi-select {
    position: relative;
    width: 100%;
  }
  
  .custom-multi-select select {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 100%;
    padding: 10px;
    border: 1px solid #ced4da;
    border-radius: 4px;
    background-color: #fff;
    cursor: pointer;
  }
  
  .custom-multi-select .dropdown-arrow {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    pointer-events: none;
  }
  
  .custom-multi-select .dropdown-arrow i {
    font-size: 14px;
    color: #495057;
  }
  